@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
/* Smartphones (portrait) ----------- */
html{
  font-size: 16px;
}

@media (min-width: 768px) {
  html {
    font-size: 18px;
  }
}
@media (min-width: 1024px) {
  html {
    font-size: 20px;
  }
}
@media (min-width: 2048px) {
  html {
    font-size: 22px;
  }
}
body {
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Roboto;
}
input {
  outline: none;
}
input[type='text'] {
  font-size:1rem;
  padding: 0.38rem;
  border: none;
  border: 1px solid #dcdcdc;
  border-radius: 5px;
}

input[type='image']:disabled {
  opacity: 0.5;
}

main {
  max-width: 800px;
}

h1 {
  text-align: center;
  font-size: 2rem;
  margin-bottom:.8rem;
}
.additem {
  display: flex;
  justify-content: center;
}

.additem-content {
  padding-right: .25rem;
}

.additem-button {
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  background-color: #ffffff;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  cursor: pointer;
  color: #000;
  font-size: 1rem;
  padding: .3rem 1.5rem;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
  outline: none;
}

.additem-button:hover {
  background: linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
  background-color: #f6f6f6;
}

.additem-button:active {
  position: relative;
  top: 1px;
}

.items {
  max-width: 400px;
  margin: .5rem auto;
  border: 1px rgb(139, 137, 137) solid;
}

.item {
  padding: .5rem;
  display: grid;
  grid-template-columns: 0.5fr 4fr 0.5fr 0.5fr;
  grid-column-gap: .25rem;
  align-items: center;
}

.item:nth-child(odd) {
  background: #f1f1f1;
}

.item-disabled {
  text-decoration: line-through;
  opacity: 0.5;
}

.item-button {
  border: none;
  outline: none;
  cursor: pointer;
  background:none;
}

.item-button:disabled {
   opacity: .5;
  }

